input {
  margin: 0;
  border: 0;
  padding: 0;
  height: 100%;

  font-size: pxToRem(32px);

  outline: none;
  &::-webkit-input-placeholder {
    color: #ccc;
  }
}

.no-border {
  &:after {
    border: none !important;
  }
}

.content-login {
  padding-top: pxToRem(130px);
  .logo1 {
    margin: 0 auto pxToRem(70px);
  }
  .form-login {
    margin: 0 pxToRem(30px);
  }
  .input-wrapper {
    // position: relative;

    // margin-bottom: pxToRem(30px);
    // height: pxToRem(90px);

    // line-height: pxToRem(90px);
    // &:after {
    //   @include border(bottom, #eeeff1);
    // }
    // i {
    //   margin-right: pxToRem(10px);

    //   vertical-align: middle;
    // }
    input {
      width: pxToRem(620px);

      // color: #333;
    }
  }
  .btn {
    margin: pxToRem(70px) auto 0;
  }
}

.content-activation {
  padding: pxToRem(80px) 0;
  .logo2 {
    margin: 0 auto;
  }
  .form-active {
    margin-top: pxToRem(82px);
    .item-wrapper {
      // position: relative;

      // padding-left: pxToRem(30px);
      // height: pxToRem(98px);

      // font-size: pxToRem(32px);
      // line-height: pxToRem(98px);

      // color: #333;
      // background-color: #fff;
      input,
      select,
      p {
        display: inline-block;
        float: right;

        box-sizing: border-box;
        padding-right: pxToRem(58px);
        width: pxToRem(580px);

        text-align: right;
      }
      &:after {
        // @include border(bottom, #e5e5e5);

        left: pxToRem(30px);
      }

      label {
        padding: 0;
        width: pxToRem(130px);

        text-align: left;
      }
      select {
        border: 0;
        height: 100%; // padding: 0;

        font-size: pxToRem(32px);

        background-color: transparent;

        outline: none;

        direction: rtl;
      }
      i {
        margin-right: pxToRem(10px);

        vertical-align: text-top;
      }
      &.item-large {
        height: auto;
        line-height: normal;
      }
      &.checkboxs {
        padding-bottom: pxToRem(14px);
        .checkbox-wrap {
          // position: relative;
          // top: 30%;

          // width: 90%;
          // height: 40%;

          // background-color: white;
          font-size: 0;
          vertical-align: middle;
        }
        fieldset legend {
            padding: pxToRem(34px) 0 pxToRem(14px);
        }
        input[type="checkbox"] {
          display: none;
        }
        input[type="checkbox"] + label {
          display: inline-block;

          // margin-top: 10px;
          // margin-left: 5px;
          margin: pxToRem(20px) 0;
          -webkit-box-sizing: border-box;
          width: 33.33%;

          text-align: left;
          font-size: pxToRem(32px);
        }

        label::before {
          display: inline-block;

          @include inline-background("data:img/jpg;base64,iVBORw0KGgoAAAANSUhEUgAAACIAAAAiBAMAAADIaRbxAAAAHlBMVEUAAADMzMzMzMzMzMzMzMzMzMzMzMzMzMzMzMzMzMwLbf+mAAAACXRSTlMA8q0RbifpdiWvaVS1AAAAOElEQVQoz2PgUJyJDIQaGMKcGZCBSSqDCgMqKGIQRhMxZJiAJsI5KjLkRTBjGTMlYKYWVrQUFQAALl0kcAxXPqkAAAAASUVORK5CYII=", 34px, 34px);

          vertical-align: sub;

          content: "";
          margin-right: pxToRem(12px);
        }
        input[type="checkbox"]:checked + label::before {

          @include inline-background("data:img/jpg;base64,", 34px, 34px);
        }
      }
    }
    .btn {
      margin-top: pxToRem(40px);
    }
    .status-non {
      color: #ccc;
      .icon-loca {
        background-position: pxToRem(0px) pxToRem(-51px);
      }
    }
    .status-fail {
      color: #f0534b;
      .icon-loca {
        width: pxToRem(28px);

        background-position: pxToRem(-34px) pxToRem(-51px);
      }
    }
    .not-select {
      color: #999;
    }
  }
}

.content-result {
  padding: pxToRem(180px) pxToRem(30px) 0;

  text-align: center;
  .icon-success {
    margin: 0 auto pxToRem(60px);
  }
  div {
    padding-bottom: pxToRem(30px);

    font-size: pxToRem(32px);
    // text-align: center;

    color: #333;
  }
  p {
    padding: 0 pxToRem(80px);

    font-size: pxToRem(26px);
    line-height: pxToRem(32px);
    // text-align: left;

    color: #999;
  }
}

.footer-result {
  position: fixed;
  right: 0;
  bottom: pxToRem(52px);
  left: 0;

  font-size: pxToRem(26px);
  line-height: pxToRem(32px);
  text-align: center;

  color: #ffbf00;
}

