.popup-location {
  display: none;

  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;

  background-color: #fff;
  .close {
    position: absolute;
    top: 60px;
    left: pxToRem(30px);
    z-index: 2000;

    border-radius: pxToRem(80px);
    width: pxToRem(80px);
    height: pxToRem(80px);

    background: url("data:img/jpg;base64,") no-repeat center;
    background-color: rgba(0, 0, 0, .55);
    background-size: pxToRem(34px) pxToRem(34px);
  }
  iframe {
    position: relative;

    width: 100%;
    height: 100%;
  }
}


// iosSelect样式覆盖
.iosselect-box {
  background-color: #fff;
}

.ios-select-widget-box header.iosselect-header a.close {
  color: #999;
}

.ios-select-widget-box header.iosselect-header a.sure {
  color: #ffbf00;
}

// icons
.logo1 {
  @include background("../images/activation/logo1.png",
  246px,
  220px);
}

.logo2 {
  @include background("../images/activation/logo2.png",
  410px,
  114px);
}

.icon-success {
  @include background("../images/activation/success.png",
  252px,
  167px);
}


// 雪碧图
.sprite {
  display: block;

  background-image: url(../images/activation/sprite.png);
  background-repeat: no-repeat;
  background-size: pxToRem(106px)  pxToRem(85px);
}
.icon-loca {
  @extend .sprite;

  display: inline-block;

  width: pxToRem(26px);
  height: pxToRem(34px);

  background-position: pxToRem(-78px) pxToRem(0px);
}
.icon-loca2 {
  @extend .sprite;

  width: pxToRem(26px);
  height: pxToRem(34px);

  background-position: pxToRem(0px) pxToRem(-51px);
}
.icon-loca3 {
  @extend .sprite;

  width: pxToRem(26px);
  height: pxToRem(34px);

  background-position: pxToRem(-36px) pxToRem(-51px);
}
.icon-lock {
  @extend .sprite;

  display: inline-block;

  width: pxToRem(30px);
  height: pxToRem(41px);

  background-position: pxToRem(0px) pxToRem(0px);
}
.icon-phone {
  @extend .sprite;

  display: inline-block;

  width: pxToRem(30px);
  height: pxToRem(40px);

  background-position: pxToRem(-40px) pxToRem(0px);
}
